			oactive:0,
			oshow:false,
			shaixs:["木工","瓦工","电工","油漆工","抹灰工","钢筋工","焊工","架子工","混凝土工","安装工","水电工","铺装工","防水工","吊顶工","管道工","普工/杂工","学徒工","项目经理","监理","劳务经理","模板工","爆破工","养护工","古建工","拆除工","工程机械","园林绿化","其它工种"],
